@import "../../../scss/helpers/placeholder";
@import "../../../scss/helpers/mixins";

$prefix: '.rise-blocks';
$text-color: #717171;

#{$prefix}-blog-wrapper {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;

	#{$prefix}-blog-card {
		box-shadow: 0 0 1px rgba(0,0,0,.1), 0 2px 6px rgba(0,0,0,.03);
	    transition: all .3s;
		height: 100%;
		background: #fff;

	    &:hover {
	    	box-shadow: 0 1px 15px rgba(0,0,0,.15);
	    }
	} 

	a {
		&:focus {
	    	outline: none;
	    	box-shadow: none;
	    }  
	}

	#{$prefix}-blog-meta-wrapper {
	    display: flex;
	    flex-wrap: wrap;
	    align-items: center;
	    border-bottom: 1px solid #cecece;
	    padding: 20px 0 13px;
	    position: relative;
	}

	#{$prefix}-blog-image{
		display: block;   
	    line-height: 0;
	    min-height: 70px;

	    img {
	    	max-width: 100%;
	    	height: auto;
	    }    
	}

	#{$prefix}-blog-body {
	    padding: 0 18px 18px 18px;
	}

	#{$prefix}-blog-post-content {
		p {
		    margin-top: 16px;
	        margin-bottom: 0;
		    color: $text-color;
		    line-height: 1.8;
		}
	} 

	#{$prefix}-blog-post-title {
	    font-size: 21px;
	    margin: 16px 0 0;

		a {
		    text-decoration: none;
		    color: #000;	    
		}
	} 

	#{$prefix}-blog-post-date {

		span {
			&:before {
			    content: "\f073";
			   	font-family: fontAwesome;
	    		padding-right: 10px;
			}
		}	
	} 

	#{$prefix}-blog-post-cat {

		span {
			&:before {
			    content: "\f02b";
			   	font-family: fontAwesome;
	    		padding-right: 10px;
			}
		}
	} 

	.meta-content {
	    font-size: 13px;
	    padding-right: 12px;

	    a {
	        color: $text-color;
	        color: inherit;
	        text-decoration: none;	
	    }
	}

	#{$prefix}-blog-author {

		img {
		    width: 45px;
		    height: 45px;
		    position: absolute;
		    border-radius: 50%;
		    overflow: hidden;
		    top: -33px;
		    border: 2px solid #fff;
		}
	} 

	#{$prefix}-blog-card-wrapper {
		padding: 15px;
		max-width: 100%;
		flex: 100%;
	}

	&#{$prefix}-item-2-per-row {
	    #{$prefix}-blog-card-wrapper {
	   		max-width: 50%;
		    flex: 50%;

		   @include media(margin-bottom) {
			   	max-width: 100%;
			   flex: 100%;
		   }
		}
	}

	&#{$prefix}-item-3-per-row {
	    #{$prefix}-blog-card-wrapper {

	   		max-width: 33.33%;
		    flex: 33.33%;

		    @include media(mb) {
			   	max-width: 100%;
			   flex: 100%;
		    }
		}
	}
	
	&#{$prefix}-item-4-per-row {
	    #{$prefix}-blog-card-wrapper {

	   		max-width: 25%;
		   	flex: 25%;
		   	
		   	@include media(xs) {
		   		max-width: 50%;
		    	flex: 50%;
		   	}

		   @include media(mb) {
			   	max-width: 100%;
			   flex: 100%;
		   }
		}
	}
}


#{$prefix}-align-center {
	text-align: center;

	div {
		justify-content: center;
	}
}

#{$prefix}-align-left {
	text-align: left;

	div {
		justify-content: flex-start;
	}
}

#{$prefix}-align-right {
	text-align: right;
	
	div {
		justify-content: flex-end;
	}
}